:root {
  /* Variables */
  --brand-green: #53C9B4;
  --brand-orange: #C9653E;
  --font-main: #3E3E3E;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: europa, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 50px;
  
  color: #3E3E3E;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.project-container {
	width: 20%;
	margin: 3%;
	padding: 25px;
}

.project-desc {
	overflow: scroll;
	height: 300px;
}

.project-container h1 {
	font-size: 0.6em;
}

.project-container p {
	font-size: 0.5em;
	margin-top: 0;
}

.project-container hr {
	border: 2px solid;
	border-radius: 2px;
}
.list-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

.section-title {
    text-align: center;
    font-size: inherit;
}
/* LANDING STYLES */
.landing-container {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.landing-content {
    margin-top: 10%;
    text-align: center;
    line-height: 20px;
    left:0; 
    right:0;
    top:0; 
    bottom:0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
    overflow: auto;
}
#logo {
    width: 250px;
    margin-bottom: 60px;
}

/* FONT STYLES */
.europa {
    font-family: europa, sans-serif;
    font-weight: 300;
    font-style: normal;
}
.heavy-font {
    font-family: europa, sans-serif;
    font-weight: 400;
    font-style: normal;
}
.green-font {
    color: var(--brand-green);
}
.orange-font {
    color: var(--brand-orange);
}
.small-font {
    font-size: 20px;
}

